'use client';

import { useState } from 'react';
import Link from 'next/link';
import { Plus, Search, FileText, Printer, CheckCircle } from 'lucide-react';

export default function ReceiptListPage() {
    // ข้อมูลจำลอง (Mock Data)
    const [receipts, setReceipts] = useState([
        { id: 1, doc_number: 'RC-202402-001', date: '2024-02-10', customer: 'บริษัท เอ บี ซี จำกัด', amount: 107000, status: 'completed' },
        { id: 2, doc_number: 'RC-202402-002', date: '2024-02-11', customer: 'นายสมชาย ใจดี', amount: 5350, status: 'completed' },
        { id: 3, doc_number: 'RC-202402-003', date: '2024-02-12', customer: 'หจก. การค้าไทย', amount: 26750, status: 'cancelled' },
    ]);

    // ✅ แก้ไขตรงนี้: ใช้แค่ div ธรรมดา (ไม่ต้องมี min-h-screen หรือ flex)
    // ระบบ Layout หลักจะจัดการ Sidebar และพื้นหลังให้เองครับ
    return (
        <div className="p-6"> 
            
            {/* ส่วนหัว (Header) */}
            <div className="flex justify-between items-center mb-6">
                <div>
                    <h1 className="text-2xl font-bold flex items-center gap-2 text-gray-800">
                        <FileText className="text-green-600" /> ใบเสร็จรับเงิน (Receipts)
                    </h1>
                    <p className="text-gray-500 text-sm">จัดการเอกสารการรับเงินและประวัติการชำระเงิน</p>
                </div>
                <button className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-lg flex items-center gap-2 shadow-sm transition">
                    <Plus size={20} /> ออกใบเสร็จรับเงินใหม่
                </button>
            </div>

            {/* การ์ดสรุปยอด (Stats Cards) */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
                <div className="bg-white p-4 rounded-xl shadow-sm border border-gray-200 flex items-center justify-between">
                    <div>
                        <p className="text-gray-500 text-sm">เก็บเงินแล้ว (เดือนนี้)</p>
                        <p className="text-2xl font-bold text-green-600">฿139,100.00</p>
                    </div>
                    <div className="p-3 bg-green-50 rounded-full text-green-600">
                        <CheckCircle size={24} />
                    </div>
                </div>
                <div className="bg-white p-4 rounded-xl shadow-sm border border-gray-200 flex items-center justify-between">
                    <div>
                        <p className="text-gray-500 text-sm">จำนวนเอกสาร</p>
                        <p className="text-2xl font-bold text-gray-800">3 ใบ</p>
                    </div>
                    <div className="p-3 bg-blue-50 rounded-full text-blue-600">
                        <FileText size={24} />
                    </div>
                </div>
            </div>

            {/* ตารางรายการ (Table) */}
            <div className="bg-white border border-gray-200 rounded-xl overflow-hidden shadow-sm">
                
                {/* ช่องค้นหา */}
                <div className="p-4 border-b border-gray-100 bg-gray-50/50">
                    <div className="relative max-w-md">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" size={20} />
                        <input 
                            type="text" 
                            placeholder="ค้นหาเลขที่ใบเสร็จ, ชื่อลูกค้า..." 
                            className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 text-sm"
                        />
                    </div>
                </div>

                <table className="w-full text-left border-collapse">
                    <thead className="bg-gray-50 text-gray-600 text-sm uppercase font-semibold">
                        <tr>
                            <th className="p-4 border-b">เลขที่เอกสาร</th>
                            <th className="p-4 border-b">วันที่</th>
                            <th className="p-4 border-b">ลูกค้า</th>
                            <th className="p-4 border-b text-right">ยอดสุทธิ (บาท)</th>
                            <th className="p-4 border-b text-center">สถานะ</th>
                            <th className="p-4 border-b text-center">จัดการ</th>
                        </tr>
                    </thead>
                    <tbody className="text-gray-700">
                        {receipts.map((rc) => (
                            <tr key={rc.id} className="hover:bg-gray-50 transition border-b last:border-b-0">
                                <td className="p-4 font-medium text-green-700">
                                    <Link href={`/accounting/receipts/${rc.id}`} className="hover:underline">
                                        {rc.doc_number}
                                    </Link>
                                </td>
                                <td className="p-4">{rc.date}</td>
                                <td className="p-4">{rc.customer}</td>
                                <td className="p-4 text-right font-bold">{rc.amount.toLocaleString()}</td>
                                <td className="p-4 text-center">
                                    {rc.status === 'completed' ? (
                                        <span className="bg-green-100 text-green-700 px-3 py-1 rounded-full text-xs font-bold">สำเร็จ</span>
                                    ) : (
                                        <span className="bg-gray-100 text-gray-500 px-3 py-1 rounded-full text-xs font-bold">ยกเลิก</span>
                                    )}
                                </td>
                                <td className="p-4 text-center">
                                    <div className="flex justify-center gap-2">
                                        {/* ลิงก์ไปยังหน้าดูรายละเอียด/พิมพ์ */}
                                        <Link 
                                            href={`/accounting/receipts/${rc.id}`}
                                            className="p-2 text-gray-500 hover:text-blue-600 hover:bg-blue-50 rounded-lg transition"
                                            title="ดูรายละเอียด / พิมพ์"
                                        >
                                            <Printer size={18} />
                                        </Link>
                                    </div>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
}